import ScrollReveal from "scrollreveal";
ScrollReveal({
  easing: "ease",
});


ScrollReveal().reveal('.faq__list--home .faq__item', {
  duration: 1000,
  distance: '20px',
  origin: 'top',
  interval: 1400,
  beforeReveal: (el) => {
    el.classList.add('revealed');
  }
});


ScrollReveal().reveal(".header--home", {
  duration: 600,
  distance: "100%",
  delay: 600,
  origin: "top"
});


ScrollReveal().reveal(".hero__content > h1", {
  duration: 700,
  distance: "20px",
  delay: 320,
  origin: "top"
});

ScrollReveal().reveal(".hero__content > .btn", {
  duration: 800,
  distance: "30px",
  delay: 650,
  origin: "bottom"
});

ScrollReveal().reveal(".hero__inner > .social", {
  duration: 1300,
  distance: "20px",
  delay: 1150,
  origin: "top"
});

ScrollReveal().reveal(".hero__inner > .lang, .wrapper > .link, .wrapper > p", {
  duration: 1300,
  distance: "20px",
  delay: 1450,
  origin: "top"
});

ScrollReveal().reveal(".header, .section-heading, .title, .services__inner, .portfolio__inner, .reviews__inner, .contacts__inner, .contacts__wrapper, .seo__inner", {
  distance: '50px',
  origin: 'top',
  duration: 1000,
  delay: 500,
});

ScrollReveal().reveal(".services__list, .portfolio__list, .stages__list, .team__list, .reviews__list, .blog__slider-wrapper, .contacts__follow", {
  duration: 1000,
  interval: 400,
  delay: 500,
});

ScrollReveal().reveal(".btn", {
  duration: 800,
  distance: "20px",
  delay: 750,
  origin: "bottom"
});

ScrollReveal().clean('.form__btn, .modal__title, .modal__close');