const openModal = [...document.querySelectorAll('.modal-btn')];
const dialog = document.querySelector('dialog')
const finishDialog = document.querySelector('dialog.modal--finish');
const closeDialogBtns = [...document.querySelectorAll('.modal__close')];

openModal.forEach(button => {
  button.addEventListener("click", () => {
    dialog?.showModal();
  });
});

closeDialogBtns.forEach(button => {
  button.addEventListener("click", () => {
    const dialog = button.closest('dialog');
    dialog?.close();
  });
});

document.querySelector('.form--modal') ?.addEventListener('submit', e => {
  e.preventDefault();
  dialog?.close();
  finishDialog.showModal();
});




